<template>
    <div>
        <h4></h4>
        <el-tabs type="border-card">
            <el-tab-pane label="">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="postLink">
                      <el-input v-model="ruleForm.postLink"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="postTitle">
                      <el-input v-model="ruleForm.postTitle"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="postImage">
                      <el-input v-model="ruleForm.postImage"></el-input>
                    </el-form-item>
  
                    <el-form-item label="" prop="postSummary">
                      <el-input type="textarea" v-model="ruleForm.postSummary"></el-input>
                    </el-form-item>

                        <el-form-item label="" prop="category">
                      <el-select v-model="ruleForm.category" placeholder="">
                        <el-option label="news" value="news"></el-option>
                        <el-option label="wtf" value="wtf"></el-option>
                        <el-option label="darwin" value="darwin"></el-option>
                        <el-option label="cheers" value="cheers"></el-option>
                        <el-option label="longreads" value="longreads"></el-option>
                        <el-option label="resource" value="resource"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                      <el-button @click="resetForm('ruleForm')"></el-button>
                    </el-form-item>
                  </el-form>
                    

            </el-tab-pane>
            <el-tab-pane label=""></el-tab-pane>
    
        </el-tabs>
     <!---->
        <modal v-if="deliverSuccess" @close="showModal = false">
          <div slot="header" style="text-align:left">
            <span style="font-size:20px;font-weight:bolder;color:#cc5a18">
              ok
            </span>
           
          </div>
          <div slot="body">
            
            <span>YES</span>
          
          </div>
         
        <div slot="footer">
          <a class="btn " href="javascript:;" @click="showModal = false" style="text-decoration:none;color:gray;margin-right:20px"></a>
          <a class="btn btn--m btn--red" href="javascript:;" style="text-decoration:none" @click="toHome">
          
          </a>
        </div>
      </modal>
    </div>
</template>

<script>
import axios from "axios";
import Modal from './../public/Modal'
export default {
  data() {
    return { 
      createMethod:Number,
      errTip: false,
      errTip01:false,
      sucTip:false,
      showModal:false,
      deliverSuccess:false,
      ruleForm: {
        postLink: '',
        postTitle:"",
        postImage:"",
        category:"",
        delivery: false,
        type: [],
        resource: '',
        },
      rules: {
          postLink: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 200, message: '', trigger: 'blur' }
          ],
          postTitle: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 50, message: ' 3  50', trigger: 'blur' }
          ],
           postSummary: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 500, message: '500', trigger: 'blur' }
          ],
          category: [
            { required: true, message: '', trigger: 'change' }
          ],
         
          resource: [
            { required: true, message: '', trigger: 'change' }
          ],
          desc: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      
    };
  },

  components:{
    Modal
  },

  mounted(){
 
  },

  methods: {
 
    deliver(){
      this.sucTip=false;
     this.$refs["formone"].restFields()

    },
    toHome(){
     this.$router.push({
       path:"/"})
   },
    reset(){
      document.querySelector("#words").reset()
    },
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) { 
                       this.$message({
                          message: 'congratulations',
                          type: 'success'
                          });
                     
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
  }
};
</script>



